@charset "UTF-8";

/*Обнуление*/

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/*--------------------*/

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.wraper-header {
  margin-top: 20px;
  font-size: 50px;
  color: blue;
}

.main {
  flex: 1 1 auto;
  color: yellowgreen;
  font-size: 25px;
}

.wraper-main {
  color: red;
  font-size: 20px;
  display: flex;
  display: grid;
  grid-column: 34;
  grid-template-columns: auto;
  grid-area: 34;
}

.test-img {
  height: 20%;
  width: 20%;
}

.wraper-footer {
  color: red;
  font-size: 80px;
  display: flex;
  display: grid;
  grid-column: 34;
  grid-template-columns: auto;
  grid-area: 34;
}

body {
  height: 100%;
  line-height: 1;
}

.wraper-all {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}